import { FC } from 'react';
import { Link } from 'react-router-dom';

import { ReactComponent as CartIco } from '../../../assets/cart.svg';

import './buttons.css';

interface Props {
	count: number;
	totalPrice: number;
}

const Cart: FC<Props> = ({ count, totalPrice }) => {
	return (
		<div className="cart nav__cart">
			<Link to="/cart">
				<CartIco className="cart__ico" />
				<div className="cart__counter">{count}</div>
				<div className="cart__price">{`${totalPrice.toFixed(2)} zł`}</div>
			</Link>
		</div>
	);
};

export default Cart;
