import { Dispatch, FC } from 'react';
import { Link } from 'react-router-dom';
import classNames from 'classnames';

import { ReactComponent as Hamburger } from '../../../assets/menu.svg';
import { ReactComponent as Close } from '../../../assets/close.svg';

import './menu.css';

interface HeaderProps {
	show: boolean;
	items: string[][];
	staggerStep: number;
	inTransition: boolean;
}
interface NavProps extends HeaderProps {
	setShow: Dispatch<boolean>;
}

export const NavMenu: FC<NavProps> = ({ show, setShow, items }) => {
	return (
		<>
			<button className="nav__burger" type="button" onClick={() => setShow(!show)}>
				{show ? <Close /> : <Hamburger />}
			</button>
			<ul className="nav__menu-list menu-list">
				{items.map(([item, url]) => (
					<li className="nav__menu-item menu-item" key={item}>
						<Link to={url}>{item}</Link>
					</li>
				))}
			</ul>
		</>
	);
};

export const HeaderMenu: FC<HeaderProps> = ({ show, items, staggerStep, inTransition }) => {
	const calcDelay = (i: number, length: number) =>
		inTransition ? 0 : (show ? i : length - i - 1) * staggerStep;

	return (
		<nav
			className={classNames(
				'header__menu',
				show ? 'header__menu--visible' : 'header__menu--hidden'
			)}>
			<ul className="header__menu-list menu-list">
				{items.map(([item, url], i) => (
					<li
						key={item}
						className="header__menu-item menu-item"
						style={{
							transitionDelay: `${calcDelay(i, items.length)}s`
						}}>
						<Link to={url}>{item}</Link>
					</li>
				))}
			</ul>
		</nav>
	);
};

